*{
    overflow: hidden;
}

.area-container{
    height: 100vh;
    width: 100vw;
}

.title-main{
    position: absolute;
    top: 30px;
    left: 50px;
    font-size: 4rem;
    letter-spacing: 10px;
    font-weight: 600;
    color: white;
    text-shadow:  -2px -2px 0 #333,  
    1px -1px 0 #333,
    -1px 1px 0 #333,
     1px 1px 0 #333;
     text-transform: uppercase;
}

.area{
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

::-webkit-scrollbar {
    width: 10px;
  }
  
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 0px #333; 
    border-radius: 10px;
  }   

::-webkit-scrollbar-thumb {
    background: white; 
    border-radius: 3px;
    border: 3px solid #333;
  }
  
::-webkit-scrollbar-thumb:hover {
    background: none; 
  }

.area .container{
    height: 75vh;
    width: 75vw;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 50px;
    justify-content: center;
    align-items: center;
    padding: 0px 50px 50px 50px;
    margin-top: 90px;
    overflow-y: scroll;
}

.area .card{
    background: white;
    height: 250px;
    padding: 20px 25px;
    border-radius: 5px;
    border: 3px solid #333;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    text-transform: uppercase;
    letter-spacing: 5px;
    font-weight: 600;
    transition-property: all;
    transition-duration: 300ms;
}

.card:hover{
    font-size: 2.5rem;
        color: white;
    text-shadow:  -2px -2px 0 #333,  
    1px -1px 0 #333,
    -1px 1px 0 #333,
     1px 1px 0 #333;
}

